import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Button from 'components/FormsV3/Button/Button';
import TextInput from '../Forms/TextInput/TextInput';

import SocialSignIn from '../Templates/SocialSignIn/SocialSignIn';

import { initLoading, signInEmailPassword } from '../../actions/userActions';

import './Signing.scss';

class Login extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.defaultErrorMsg !== prevState.defaultErrorMsg) {
      return ({
        defaultErrorMsg: nextProps.defaultErrorMsg,
        isUpdateHeight: true,
      });
    }

    return null;
  }
  constructor(props) {
    super(props);

    this.state = {
      defaultErrorMsg: null,
      isUpdateHeight: false,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidUpdate() {
    const { isUpdateHeight } = this.state;
    const { updateTabHeight } = this.props;

    if (isUpdateHeight && updateTabHeight) {
      updateTabHeight();

      this.setState({
        isUpdateHeight: false,
      });
    }
  }

  async handleSubmit(e) {
    const {
      closePopUp,
      loginUser,
      setLoading,
      callback,
    } = this.props;

    setLoading();
    loginUser(e, null, () => {
      closePopUp();
      setTimeout(callback, 400);
    });
  }

  render() {
    const {
      defaultErrorMsg,
    } = this.state;

    const {
      form,
      change,
      handleSubmit,
      goToIndex,
      isLoading,
      callback,
      closePopUp,
      storeForm,
    } = this.props;

    let errorString = defaultErrorMsg;
    errorString = errorString === 'userId and password are required' ? 'Email and password are required' : errorString;
    errorString = errorString === 'user credentials are invalid' ? 'User credentials are invalid' : errorString;

    return (
      <Row
        className="login-container"
        noGutters
        onKeyPress={(e) => {
          if (e.key === 'Enter') {
            e.preventDefault();
            this.handleSubmit(storeForm);
          }
        }}
      >
        <Col xs={12}>
          <p className="welcome-text">Welcome Back!</p>
        </Col>
        <Col xs={12}>
          <SocialSignIn
            socialType="FACEBOOK"
            onSuccess={() => {
              closePopUp();
              setTimeout(callback, 400);
            }}
          />
        </Col>
        <Col xs={12}>
          <SocialSignIn
            socialType="GOOGLE"
            onSuccess={() => {
              closePopUp();
              setTimeout(callback, 400);
            }}
          />
        </Col>
        <Col className="seperator" xs={12}>
          <hr />
          <p>or</p>
        </Col>
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="email"
              placeholderString="Email"
              size="medium"
              zIndex={90}
            />
          </Col>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="password"
              placeholderString="Password"
              size="medium"
              zIndex={89}
              isPassword
            />
          </Col>
          <Col className="forget-pass" xs={12}>
            <button
              className="default"
              onClick={(e) => { e.preventDefault(); goToIndex(0); }}
            >
              Forgot password?
            </button>
          </Col>
          {
            errorString ?
              <Col xs={12}>
                <p className="errorString">{errorString}</p>
              </Col>
            :
              null
          }
          <Col xs={12}>
            <Button
              type="submit"
              variant="medium-color"
              width={345}
              height={72}
              isLoading={isLoading}
            >
              <p>Sign In</p>
            </Button>
          </Col>
        </form>
        <Col className="seperator" xs={12}>
          <hr />
        </Col>
        <Col xs={12}>
          <span className="no-account">{'Don\'t have an account? '}</span>
          <button
            className="sign-up link-bold"
            onClick={(e) => { e.preventDefault(); goToIndex(2); }}
          >
            Sign up
          </button>
        </Col>
      </Row>
    );
  }
}

Login.defaultProps = {
  closePopUp: null,
  updateTabHeight: null,
  isLoading: null,
  callback: () => {},
  storeForm: {},
};

Login.propTypes = {
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  callback: PropTypes.func,
  handleSubmit: PropTypes.func.isRequired,
  goToIndex: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  closePopUp: PropTypes.func,
  updateTabHeight: PropTypes.func,
  storeForm: PropTypes.object,
};

const mapStateToProps = state => ({
  isRevert: state.editForm.isRevert,
  defaultErrorMsg: state.userDetails.defaultErrorMsg,
  isLoading: state.userDetails.isLoading,
  storeForm: getFormValues('login')(state),
});

const mapDispatchToProps = dispatch => ({
  loginUser: (params, onFail, onSuccess) => (
    dispatch(signInEmailPassword(params, onFail, onSuccess))
  ),
  setLoading: () => (
    dispatch(initLoading())
  ),
});

export default reduxForm({
  form: 'login',
})(connect(mapStateToProps, mapDispatchToProps)(Login));
