/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Field, change, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

import MagnifyGlass from 'assets/images/forms/magnifyGlass.svg';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import { getPostal } from '../../../actions/insuranceActions';

import './TextInput.scss';

// Height for medium dropdown
const mediumHeight = 60;
// Height for big dropdown
const bigHeight = 72;

/**
 * Custom Form Dropdown class
 */
class TextInput extends Component {
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    // dropList - The passed down dropdown list items
    // placeholderString - Placeholder string for input
    // size - Size of this control
    // numItemsDisplay - Max items to display on view
    const {
      size,
      height,
    } = this.props;

    // Get the control height
    let heightToUse = size === 'big' ? bigHeight : mediumHeight;
    heightToUse = height || heightToUse;

    // isOpened - Whether dropdown is opened
    // listItems - The actual dropdown list
    // selectedString - The selected item string
    // height - Control height
    // numItemsDisplay - How many items to display
    // maxheight - Max height of the list
    // listHeight - Height of the list. (Items count + control)
    this.state = {
      height: heightToUse,
      isFocus: false,
    };

    this.handleFocus = this.handleFocus.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.setError = this.setError.bind(this);
    this.clearError = this.clearError.bind(this);
    this.renderTextArea = this.renderTextArea.bind(this);
    this.handlePostCode = this.handlePostCode.bind(this);
  }

  setError(message) {
    this.formParent.setErrorString(message);
  }

  clearError() {
    this.formParent.clearErrorString();
  }

  handleFocus() {
    this.setState({
      isFocus: true,
    });
  }

  handleBlur() {
    this.setState({
      isFocus: false,
    });
  }

  async handlePostCode(e) {
    e.preventDefault();
    const {
      storeValue,
      name,
      addressFields,
      formChange,
      form,
    } = this.props;

    if (storeValue) {
      const postcode = storeValue[name];
      const result = await getPostal(postcode);
      if (result) {
        // const streetName = result.name === '' ? '' : `${result.name} `;
        const street = result.street === '' ? '' : `${result.street} `;
        const block = result.block === '' ? '' : `${result.block} `;
        // let concatAddress = `${block}${streetName}${street}`;
        let concatAddress = `${block}${street}`;

        if (addressFields.blkNo) {
          formChange(form, addressFields.blkNo, result.block);
          concatAddress = street;
        }

        if (addressFields.address) {
          formChange(form, addressFields.address, concatAddress);
        }
      }
    }
  }

  renderTextArea({ input }) {
    const { placeholderString } = this.props;
    return (
      <div className="text-area">
        <textarea {...input} placeholder={placeholderString} rows="10" cols="40" />
      </div>
    );
  }

  render() {
    const {
      height,
      isFocus,
    } = this.state;

    const {
      name,
      desc,
      placeholderString,
      zIndex,
      isPassword,
      extraInfo,
      errorString,
      isTextArea,
      storeValue,
      validateForm,
      isPostCode,
      toUpper,
    } = this.props;

    const focusStyle = isFocus ? 'focus' : '';
    const passwordType = isPassword ? 'password' : '';
    const errorStyle = errorString ? 'error' : '';

    const refValue = storeValue ? storeValue[name] : null;

    return (
      <FormInputWrapper
        desc={desc}
        ref={node => (this.formParent = node)}
        extraInfo={extraInfo}
        errorString={errorString}
        refValue={refValue}
        validateForm={validateForm}
      >
        <div
          className={`text-input-container ${focusStyle} ${errorStyle}`}
          style={{ height, zIndex }}
          onFocus={() => this.handleFocus()}
          onBlur={() => this.handleBlur()}
        >
          <div className="text-input">
            {
              isTextArea ?
                <Field
                  className="text"
                  component={this.renderTextArea}
                  name={name}
                  normalize={value => (value && toUpper ? value.toUpperCase() : value)}
                />
                :
                  <Field
                    className="text"
                    component="input"
                    type={passwordType}
                    placeholder={placeholderString}
                    name={name}
                    normalize={value => (value && toUpper ? value.toUpperCase() : value)}
                  />
            }
            {
              isPostCode ?
                <button
                  className="postcode"
                  onClick={e => this.handlePostCode(e)}
                >
                  <img src={MagnifyGlass} alt="magGlass.svg" />
                </button>
              :
                null
            }
          </div>
        </div>
      </FormInputWrapper>
    );
  }
}

TextInput.defaultProps = {
  desc: null,
  isPassword: false,
  extraInfo: null,
  errorString: null,
  isTextArea: false,
  height: null,
  storeValue: null,
  isPostCode: false,
  addressFields: null,
  validateForm: null,
  toUpper: false,
};


TextInput.propTypes = {
  placeholderString: PropTypes.string.isRequired,
  size: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  zIndex: PropTypes.number.isRequired,
  desc: PropTypes.string,
  isPassword: PropTypes.bool,
  extraInfo: PropTypes.object,
  errorString: PropTypes.string,
  isTextArea: PropTypes.bool,
  height: PropTypes.number,
  validateForm: PropTypes.func,
  storeValue: PropTypes.object,
  isPostCode: PropTypes.bool,
  addressFields: PropTypes.object,
  toUpper: PropTypes.bool,
};

const mapStateToProps = (state, ownProps) => ({
  storeValue: (getFormValues(ownProps.form)(state)),
});

const mapDispatchToProps = dispatch => ({
  formChange: (form, key, value) => {
    dispatch(change(form, key, value));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(TextInput);
