/**
 * TabView.js
 * TabView that uses a carousel or fading.
 *
 * For carousel, it will take in multiple elements in
 * an array to swap when changed tabs.
 *
 * For fading view, it will only take the 1st element in the array,
 * but it does a fading to simulate and give time for the change of state
 * to happen in that element.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { Carousel } from 'react-responsive-carousel';

import PropTypes from 'prop-types';

import DropDownV2 from '../../Forms/DropDown/DropDownV2';

import { setTabSelection } from '../../../actions/tabActions';

import './TabView.scss';

/**
 * Custom Form Dropdown class
 */
class TabView extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      currentTabSelection,
      tabs,
      maxTabWidthPx,
      isDataFadeOnly,
      padding,
      children,
    } = nextProps;

    const { activeIndex } = prevState;

    let result = null;

    if (tabs && currentTabSelection) {
      const newIndex = _.findIndex(tabs, tab => tab.desc === currentTabSelection);
      if (newIndex >= 0 && newIndex !== activeIndex) {
        result = {
          activeIndex: newIndex,
        };
      }
    }

    if (tabs !== prevState.tabs && children) {
      const isUsingMaxWidth = maxTabWidthPx > 0;
      const maxWidth = (100 - padding) / (isDataFadeOnly ? tabs.length : children.length);
      const tabWidth = isUsingMaxWidth ? maxTabWidthPx : maxWidth;

      result = {
        ...result,
        tabs,
        width: tabWidth,
        widthPercentage: isUsingMaxWidth ? `${maxTabWidthPx}px` : `${tabWidth}%`,
        maxWidth: `${maxWidth}%`,
      };
    }

    return result;
  }

  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    const {
      padding,
      children,
      maxTabWidthPx,
      isDataFadeOnly,
      tabs,
      initialIndex,
    } = this.props;

    const isUsingMaxWidth = maxTabWidthPx > 0;
    const maxWidth = (100 - padding) / (isDataFadeOnly ? tabs.length : children.length);
    const tabWidth = isUsingMaxWidth ? maxTabWidthPx : maxWidth;

    this.state = {
      activeIndex: initialIndex,
      containerWidth: 0,
      width: tabWidth,
      widthPercentage: isUsingMaxWidth ? `${maxTabWidthPx}px` : `${tabWidth}%`,
      maxWidth: `${maxWidth}%`,
      paddingWidth: maxTabWidthPx ? '100%' : `${padding}%`,
      isControlVisible: true,
      isUsingMaxWidth,
      isDropDownControl: tabs && tabs.length > 10,
      fadeStyle: '',
      tabs,
    };

    this.goToIndex = this.goToIndex.bind(this);

    this.handleResize = this.handleResize.bind(this);
    this.resizeTabHeight = this.resizeTabHeight.bind(this);
    this.renderTabs = this.renderTabs.bind(this);
    this.renderTabContent = this.renderTabContent.bind(this);
    this.renderControls = this.renderControls.bind(this);
    this.renderSlides = this.renderSlides.bind(this);
  }

  componentDidMount() {
    const { tabs } = this.state;
    const {
      initialIndex,
      name,
      changeTab,
    } = this.props;

    changeTab(name, tabs[0].desc);

    window.addEventListener('resize', this.handleResize);

    this.handleResize();

    this.resizeTabHeight(initialIndex, ReactDOM.findDOMNode(this.carousel));

    this.setState({
      activeIndex: initialIndex,
    });
  }

  componentDidUpdate(prevProps, prevState) {
    const { tabs, activeIndex, containerWidth } = this.state;
    const { setTriggerHeightUpdate } = this.props;
    if (!prevProps.setTriggerHeightUpdate && setTriggerHeightUpdate) {
      const { resizeTabHeight } = this;

      setTriggerHeightUpdate(() => {
        resizeTabHeight(activeIndex);
      });
    }

    if (tabs !== prevState.tabs) {
      this.resizeTabHeight(activeIndex);
    }

    if (this.tabContainer) {
      const tabWidth = this.tabContainer.getBoundingClientRect().width;
      if (tabWidth !== containerWidth) {
        setTimeout(() => this.setState({ containerWidth: tabWidth }), 100);
      }
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize() {
    const { isControlVisible, activeIndex, tabs } = this.state;
    const { hideWidth } = this.props;

    this.setState({
      isDropDownControl: window.innerWidth < 560 || (tabs && tabs.length > 10),
    });


    this.resizeTabHeight(activeIndex);

    if (hideWidth) {
      const isVisibleNow = window.innerWidth > hideWidth;

      this.setState({
        isControlVisible: isVisibleNow,
      });

      if (isControlVisible && !isVisibleNow) {
        this.goToIndex(0);
      }
    }
  }

  goToIndex(newIndex, isForced = false) {
    const { tabs, activeIndex } = this.state;
    if (!isForced && newIndex === activeIndex) {
      return;
    }
    const {
      name,
      isDataFadeOnly,
      changeTab,
    } = this.props;


    if (isDataFadeOnly) {
      this.setState({
        fadeStyle: 'animated fadeOut',
      });

      this.setState({ activeIndex: newIndex });
      changeTab(name, tabs[newIndex].desc);

      setTimeout(() => {
        if (tabs[newIndex].onClick) {
          tabs[newIndex].onClick();
        }
        this.setState({
          fadeStyle: 'animated fadeIn',
        });

        setTimeout(() => {
          this.setState({
            fadeStyle: '',
          });
        }, 300);
      }, 300);
    } else {
      this.setState({ activeIndex: newIndex });
      changeTab(name, tabs[newIndex].desc);
      if (tabs[newIndex].onClick) {
        tabs[newIndex].onClick();
      }
    }

    this.resizeTabHeight(newIndex);
  }

  resizeTabHeight(index, carouselIncoming = null, overrideNegation = null) {
    const { isConstantHeight } = this.props;

    const carouselToUse = carouselIncoming !== null ? carouselIncoming : ReactDOM.findDOMNode(this.carousel);
    // No other way for now >_<
    if (!isConstantHeight && carouselToUse) {
      const list = carouselToUse.getElementsByTagName('UL')[0];
      const children = list.children[index];

      if (children) {
        const sliderWrapper = list.parentNode;
        const itemHeight = overrideNegation ? children.children[0].scrollHeight + overrideNegation : children.children[0].scrollHeight;
        sliderWrapper.style.height = itemHeight === 0 ? 1000 : `${itemHeight}px`;
      }
    }
  }

  renderTabContent(tab, activeStyle) {
    const {
      big,
    } = this.props;

    const bigTextStyle = big ? 'tab-text-big' : '';

    const isWithImg = !_.isString(tab);
    const desc = isWithImg ? tab.desc : tab;
    const img = isWithImg ? tab.img : null;

    return (
      <Fragment>
        {
          img ?
            <div className="img-container">
              <img className={activeStyle} src={img} alt={`${desc}.svg`} />
            </div>
          :
            null
        }
        <p className={`${activeStyle} ${bigTextStyle}`}>{desc}</p>
      </Fragment>
    );
  }

  renderTabs(width) {
    const { activeIndex, tabs } = this.state;
    const {
      big,
      maxTabWidthPx,
    } = this.props;

    const bigTabStyle = big ? 'tab-big' : '';

    return _.map(tabs, (tab, index) => {
      const tabStyle = index === activeIndex ? 'active' : 'inactive';
      const maxWidth = maxTabWidthPx > 0 ? maxTabWidthPx : null;

      return (
        <div
          className={`tab ${bigTabStyle}`}
          style={{ width, maxWidth }}
          key={`tab${index}`}
        >
          <div
            className="tab-inner"
            onClick={() => { this.goToIndex(index); }}
            role="button"
            tabIndex={-1}
            onKeyDown={() => {}}
          >
            {this.renderTabContent(tab, tabStyle)}
          </div>
        </div>
      );
    });
  }

  renderSlides() {
    const { children } = this.props;
    const { goToIndex } = this;

    return _.map(children, (item, index) => (
      <div key={`tab-item${index}`}>
        {React.cloneElement(item, {
          updateTabHeight: (overrideNegation = null) => this.resizeTabHeight(index, null, overrideNegation),
          goToIndex,
        })}
      </div>
    ));
  }

  renderControls() {
    const {
      activeIndex,
      width,
      widthPercentage,
      maxWidth,
      containerWidth,
      paddingWidth,
      isUsingMaxWidth,
      isDropDownControl,
      tabs,
    } = this.state;

    const {
      style,
      isDisableDropdown,
      big,
      charged,
    } = this.props;

    if (isDisableDropdown || !isDropDownControl) {
      const isOverContainer = tabs && tabs.length * width > containerWidth;
      const selectedMargin = isUsingMaxWidth && !isOverContainer ? activeIndex * width : `${activeIndex / tabs.length * 100}%`;
      const bigStyle = big ? 'tab-big' : '';
      const tabStyle = charged ? 'tab-container-charged' : '';

      return (
        <div
          style={style}
          className={`tab-container ${tabStyle} tab-container-padding`}
          ref={node => this.tabContainer = node}
        >
          {this.renderTabs(isOverContainer ? maxWidth : widthPercentage)}
          <div className="tab tab-padding" style={{ width: paddingWidth }} />
          <div
            className={`selected-tab ${bigStyle}`}
            style={{
              width: widthPercentage,
              maxWidth,
              marginLeft: selectedMargin,
            }}
          />
        </div>
      );
    }

    const droplist = _.map(tabs, tab => tab.desc);

    if (!isDisableDropdown && isDropDownControl) {
      return (
        <div className="tab-container">
          <DropDownV2
            ref={node => this.dropdown = node}
            placeholderString="Enter or select your destinations"
            dropList={droplist}
            size="medium"
            zIndex={100}
            isDisableTyping
            notForm
            valueChangeCallback={value => this.goToIndex(_.findIndex(tabs, tab => tab.desc === value))}
            overrideValue={tabs[activeIndex].desc}
          />
        </div>
      );
    }

    return null;
  }

  render() {
    const {
      activeIndex,
      isControlVisible,
      fadeStyle,
    } = this.state;

    const { isDataFadeOnly, className, charged } = this.props;
    const containerStyle = charged ? 'tabview-container-charged' : '';

    return (
      <div className={`tabview-container ${containerStyle} ${className}`}>
        {
          isControlVisible ?
            this.renderControls()
          :
            null
        }
        <div className={`tab-items-container ${fadeStyle}`}>
          {
            isDataFadeOnly ?
              this.props.children
            :
              <Carousel
                showThumbs={false}
                showIndicators={false}
                showArrows={false}
                showStatus={false}
                selectedItem={activeIndex}
                swipeable={false}
                onChange={index => this.goToIndex(index)}
                ref={node => (this.carousel = node)}
              >
                {this.renderSlides()}
              </Carousel>
          }
        </div>
      </div>
    );
  }
}

TabView.defaultProps = {
  padding: 0,
  hideWidth: 0,
  maxTabWidthPx: 0,
  style: null,
  isDisableDropdown: false,
  isConstantHeight: false,
  initialIndex: 0,
  setTriggerHeightUpdate: null,
  isDataFadeOnly: false,
  big: false,
  className: null,
  charged: null,
};

TabView.propTypes = {
  padding: PropTypes.number,
  tabs: PropTypes.array.isRequired,
  hideWidth: PropTypes.number,
  maxTabWidthPx: PropTypes.number,
  style: PropTypes.object,
  isDisableDropdown: PropTypes.bool,
  isConstantHeight: PropTypes.bool,
  initialIndex: PropTypes.number,
  setTriggerHeightUpdate: PropTypes.func,
  isDataFadeOnly: PropTypes.bool,
  big: PropTypes.bool,
  name: PropTypes.string.isRequired,
  className: PropTypes.string,
  charged: PropTypes.bool,
  changeTab: PropTypes.func.isRequired,
};

const mapStateToProps = (state, props) => ({
  currentTabSelection: state.tab[props.name],
});

const mapDispatchToProps = dispatch => ({
  changeTab: (tabName, value) => {
    dispatch(setTabSelection(tabName, value));
  },
});

export default connect(mapStateToProps, mapDispatchToProps, null, { withRef: true })(TabView);
