/**
 * NewSignUp.js
 * New sign up in the login module
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import SocialSignIn from 'components/Templates/SocialSignIn/SocialSignIn';
import Button from 'components/FormsV3/Button/Button';
import TextInput from '../Forms/TextInput/TextInput';

import {
  SIGNUP_SUCCESS,
  initLoading,
  signUp,
} from '../../actions/userActions';

import './Signing.scss';

class NewSignUp extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.signUpErrorMsg !== prevState.signUpErrorMsg) {
      return ({
        signUpErrorMsg: nextProps.signUpErrorMsg,
        isUpdateHeight: true,
      });
    }

    return null;
  }
  constructor(props) {
    super(props);

    this.state = {
      signUpErrorMsg: null,
      isUpdateHeight: false,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidUpdate() {
    const { isUpdateHeight } = this.state;
    const { updateTabHeight } = this.props;

    if (isUpdateHeight && updateTabHeight) {
      updateTabHeight();

      this.setState({
        isUpdateHeight: false,
      });
    }
  }

  async handleSubmit(e) {
    const {
      newSignUp,
      setLoading,
      goToIndex,
    } = this.props;

    setLoading();
    const result = await newSignUp(e, null, null);

    if (result.type === SIGNUP_SUCCESS) {
      goToIndex(1);
    }
  }

  renderSocialButton(onClick, isGoogle) {
    return (
      <button
        className={isGoogle ? 'google' : 'facebook'}
        onClick={onClick}
      >
        {isGoogle ? 'Google' : 'Facebook'}
      </button>
    );
  }

  render() {
    const { signUpErrorMsg } = this.state;
    const {
      form,
      change,
      handleSubmit,
      goToIndex,
      isLoading,
      storeForm,
      closePopUp,
    } = this.props;

    return (
      <Row
        className="login-container"
        noGutters
        onKeyPress={(e) => {
          if (e.key === 'Enter') {
            e.preventDefault();
            this.handleSubmit(storeForm);
          }
        }}
      >
        <Col xs={12}>
          <p className="welcome-text">New user sign up</p>
        </Col>
        <Col xs={12}>
          <span className="caption">Earn 5% cashback when you get protected with PolicyPal. </span>
          <a className="caption link-bold" href="/cashback" target="_blank">Learn more</a>
        </Col>
        <Col className="seperator seperator-less-margin" xs={12}>
          <hr />
        </Col>
        <Col className="seperator" xs={12}>
          <span>Sign Up with{' '}
            <SocialSignIn
              socialType="FACEBOOK"
              onSuccess={() => {
                closePopUp();
              }}
              render={this.renderSocialButton}
            />
            {' or '}
            <SocialSignIn
              socialType="GOOGLE"
              onSuccess={() => {
                closePopUp();
              }}
              render={this.renderSocialButton}
            />
          </span>
        </Col>
        <Col className="seperator" xs={12}>
          <hr />
        </Col>
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="email"
              placeholderString="Email"
              size="medium"
              zIndex={90}
            />
          </Col>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="password"
              placeholderString="Password"
              size="medium"
              zIndex={89}
              isPassword
            />
          </Col>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="password2"
              placeholderString="Confirm Password"
              size="medium"
              zIndex={89}
              isPassword
            />
          </Col>
          <Col className="terms-container" xs={12}>
            <span>By clicking “Sign Up”, you agree to our </span>
            <a href="/tos" target="_blank" className="link-color">Terms of Use</a>
            <span> and </span>
            <a href="/privacypolicy" target="_blank" className="link-color">Privacy Policy</a>
          </Col>
          {
            signUpErrorMsg ?
              <Col xs={12}>
                <p className="errorString">{signUpErrorMsg}</p>
              </Col>
            :
              null
          }
          <Col xs={12}>
            <Button
              type="submit"
              variant="medium-color"
              width={345}
              height={72}
              isLoading={isLoading}
            >
              <p>Sign Up</p>
            </Button>
          </Col>
        </form>
        <Col className="seperator" xs={12}>
          <hr />
        </Col>
        <Col xs={12}>
          <span>Already have a PolicyPal account? </span>
          <button
            className="link-bold"
            onClick={(e) => { e.preventDefault(); goToIndex(1); }}
          >
            Sign In
          </button>
        </Col>
      </Row>
    );
  }
}

NewSignUp.defaultProps = {
  updateTabHeight: null,
  isLoading: null,
  storeForm: {},
  closePopUp: null,
};

NewSignUp.propTypes = {
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  goToIndex: PropTypes.func.isRequired,
  updateTabHeight: PropTypes.func,
  isLoading: PropTypes.bool,
  storeForm: PropTypes.object,
  closePopUp: PropTypes.func,
  newSignUp: PropTypes.func.isRequired,
  setLoading: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  isRevert: state.editForm.isRevert,
  signUpErrorMsg: state.userDetails.signUpErrorMsg,
  isLoading: state.userDetails.isLoading,
  storeForm: getFormValues('newSignUp')(state),
});

const mapDispatchToProps = dispatch => ({
  newSignUp: (params, onFail, onSuccess) => (
    dispatch(signUp(params, onFail, onSuccess))
  ),
  setLoading: () => (
    dispatch(initLoading())
  ),
});

export default reduxForm({
  form: 'newSignUp',
})(connect(mapStateToProps, mapDispatchToProps)(NewSignUp));
