/**
 * ForgetPassword.js
 * Forget password view to reset password in the login module
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Button from 'components/FormsV3/Button/Button';
import TextInput from '../Forms/TextInput/TextInput';

import { forgetPassword } from '../../actions/userActions';

import './Signing.scss';

class ForgetPassword extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.resetEmailErrorMsg !== prevState.resetEmailErrorMsg) {
      return ({
        resetEmailErrorMsg: nextProps.resetEmailErrorMsg,
        isUpdateHeight: true,
      });
    }

    return null;
  }
  constructor(props) {
    super(props);

    this.state = {
      resetEmailErrorMsg: null,
      isUpdateHeight: false,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidUpdate() {
    const { isUpdateHeight } = this.state;
    const { updateTabHeight } = this.props;

    if (isUpdateHeight && updateTabHeight) {
      updateTabHeight();

      this.setState({
        isUpdateHeight: false,
      });
    }
  }

  async handleSubmit(e) {
    const {
      forgetPass,
    } = this.props;

    forgetPass(e.email, null, null);
  }

  render() {
    const { resetEmailErrorMsg } = this.state;
    const {
      form,
      change,
      handleSubmit,
      goToIndex,
      storeForm,
    } = this.props;

    return (
      <Row
        className="login-container"
        noGutters
        onKeyPress={(e) => {
          if (e.key === 'Enter') {
            e.preventDefault();
            this.handleSubmit(storeForm);
          }
        }}
      >
        <Col xs={12}>
          <p className="welcome-text">Forgot Password</p>
        </Col>
        <Col xs={12}>
          <span className="caption">Enter email and we will reset it for you. </span>
        </Col>
        <Col className="seperator seperator-less-margin" xs={12}>
          <hr />
        </Col>
        <form onSubmit={handleSubmit(this.handleSubmit)}>
          <Col xs={12}>
            <TextInput
              form={form}
              change={change}
              name="email"
              placeholderString="Email"
              size="medium"
              zIndex={90}
            />
          </Col>
          {
            resetEmailErrorMsg ?
              <Col xs={12}>
                <p className="errorString">{resetEmailErrorMsg}</p>
              </Col>
            :
              null
          }
          <Col xs={12}>
            <Button
              type="submit"
              variant="medium-color"
              width={345}
              height={72}
            >
              <p>Reset Password</p>
            </Button>
          </Col>
        </form>
        <Col className="seperator" xs={12}>
          <hr />
        </Col>
        <Col xs={12}>
          <span>Back to </span>
          <button
            className="link-bold"
            onClick={(e) => { e.preventDefault(); goToIndex(1); }}
          >
            Login
          </button>
        </Col>
      </Row>
    );
  }
}

ForgetPassword.defaultProps = {
  updateTabHeight: null,
  storeForm: {},
};

ForgetPassword.propTypes = {
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  goToIndex: PropTypes.func.isRequired,
  updateTabHeight: PropTypes.func,
  storeForm: PropTypes.object,
};

const mapStateToProps = state => ({
  isRevert: state.editForm.isRevert,
  resetEmailErrorMsg: state.userDetails.resetEmailErrorMsg,
  storeForm: getFormValues('forget-password')(state),
});

const mapDispatchToProps = dispatch => ({
  forgetPass: (params, onFail, onSuccess) => (
    dispatch(forgetPassword(params, onFail, onSuccess))
  ),
});

export default reduxForm({
  form: 'forget-password',
})(connect(mapStateToProps, mapDispatchToProps)(ForgetPassword));
