/**
 * SocialSign.js
 * Sign in to Facebook and google
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { FacebookProvider, Login } from 'react-facebook';
import GoogleLogin from 'react-google-login';

import FacebookLetter from 'assets/images/Icons/FacebookLetter.png';
import GoogleLetter from 'assets/images/Icons/GoogleLetter.png';

import { FB_ID } from '../../../constants';
import { signInFacebook, signInGoogle } from '../../../actions/userActions';

import './SocialSignIn.scss';

const socialMapping = {
  FACEBOOK: {
    style: 'fb-button',
    image: FacebookLetter,
  },
  GOOGLE: {
    style: 'google-button',
    image: GoogleLetter,
  },
};

class SocialSignIn extends Component {
  constructor(props) {
    super(props);

    this.handleLogin = this.handleLogin.bind(this);
    this.renderErrormsg = this.renderErrorMsg.bind(this);
  }

  handleLogin(response) {
    const {
      socialType,
      onFail,
      onSuccess,
      signInFB,
      signInGoo,
    } = this.props;

    if (!response) {
      if (onFail) {
        onFail();
      }

      return;
    }

    if (socialType === 'FACEBOOK') {
      signInFB(response, onFail, onSuccess);
    } else if (socialType === 'GOOGLE') {
      signInGoo(response, onFail, onSuccess);
    }
  }

  renderErrorMsg() {
    const { socialType, userDetails } = this.props;
    const { fbErrorMsg, googleErrorMsg } = userDetails;
    let msg = null;

    if (socialType === 'FACEBOOK') {
      msg = fbErrorMsg;
    } else if (socialType === 'GOOGLE') {
      msg = googleErrorMsg;
    }

    return msg ? (<p className="error-msg">{msg}</p>) : null;
  }

  render() {
    const {
      socialType,
      onFail,
      render,
    } = this.props;

    const socialData = socialMapping[socialType];
    const isGoogle = socialType === 'GOOGLE';
    const socialText = isGoogle ?
      'Sign In with Google' : 'Sign In with Facebook';

    if (isGoogle) {
      return (
        <GoogleLogin
          clientId="755882793741-7fetsl2emvb92cb51allpu5pnik887mc.apps.googleusercontent.com"
          render={renderProps => ((
            render ?
              render(renderProps.onClick, isGoogle)
            :
              <div className="signing-container">
                <button
                  className={socialData.style}
                  onClick={renderProps.onClick}
                >
                  <div className="signing-inner">
                    <img src={socialData.image} alt={`${socialType}.png`} />
                    <p>{socialText}</p>
                  </div>
                </button>
                {this.renderErrorMsg()}
              </div>
          ))}
          buttonText="Login"
          onSuccess={this.handleLogin}
          onFailure={onFail}
          cookiePolicy="single_host_origin"
        />
      );
    }

    return (
      <FacebookProvider
        appId={FB_ID}
        version="v3.0"
      >
        <Login
          scope="email"
          onCompleted={this.handleLogin}
          onError={error => console.log(error)}
        >
          {({ handleClick }) => (
            render ?
              render(handleClick, isGoogle)
            :
              <div className="signing-container">
                <button
                  className={socialData.style}
                  onClick={handleClick}
                >
                  <div className="signing-inner">
                    <img src={socialData.image} alt={`${socialType}.png`} />
                    <p>{socialText}</p>
                  </div>
                </button>
                {this.renderErrorMsg()}
              </div>
          )}
        </Login>
      </FacebookProvider>
    );
  }
}


const mapStateToProps = state => ({
  userDetails: state.userDetails,
});

const mapDispatchToProps = dispatch => ({
  signInFB: (response, onFail, onSuccess) => (
    dispatch(signInFacebook(response, onFail, onSuccess))
  ),
  signInGoo: (response, onFail, onSuccess) => (
    dispatch(signInGoogle(response, onFail, onSuccess))
  ),
});

SocialSignIn.defaultProps = {
  onFail: null,
  onSuccess: null,
  render: null,
};

SocialSignIn.propTypes = {
  signInFB: PropTypes.func.isRequired,
  signInGoo: PropTypes.func.isRequired,
  socialType: PropTypes.string.isRequired,
  userDetails: PropTypes.object.isRequired,
  onFail: PropTypes.func,
  onSuccess: PropTypes.func,
  render: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(SocialSignIn);
