/**
 * FormInputWrapper.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './FormInputWrapper.scss';

/**
 * Custom Form Dropdown class
 */
class FormInputWrapper extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.errorString !== nextProps.errorString) {
      return {
        errorString: nextProps.errorString,
        refValue: nextProps.refValue,
      };
    }

    if (nextProps.validateForm && prevState.errorString && prevState.refValue !== nextProps.refValue) {
      nextProps.validateForm();
      return {
        errorString: null,
      };
    }
    return null;
  }
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    this.state = {
      errorString: null,
    };

    this.setErrorString = this.setErrorString.bind(this);
    this.clearErrorString = this.clearErrorString.bind(this);
  }

  setErrorString(message) {
    this.setState({
      errorString: message,
    });
  }

  clearErrorString() {
    this.setState({
      errorString: null,
    });
  }

  render() {
    const {
      errorString,
    } = this.state;

    const {
      desc,
      extraInfo,
    } = this.props;

    return (
      <div className="input-wrapper-main input-wrapper-error">
        {
          desc !== null ?
            <p className="input-wrapper-desc">{desc}</p>
          :
            null
        }
        {this.props.children}
        {
          extraInfo !== null ?
            <div className="extra-info-container">
              {extraInfo}
            </div>
          :
            null
        }
        {
          errorString !== null ?
            <p className="input-wrapper-error-text">
              {errorString}
            </p>
          :
            null
        }
      </div>
    );
  }
}

FormInputWrapper.defaultProps = {
  desc: null,
  extraInfo: null,
};


FormInputWrapper.propTypes = {
  desc: PropTypes.string,
  extraInfo: PropTypes.object,
};

export default FormInputWrapper;
